<template>
  <!-- Using components -->
  <div class="fluid-container WeddingApp">
    <img :src="title_svg" class="title_svg" />
    <div class="guide-contain">
      <img v-for="item in photolist" :key="item.id" :src="item.url" class="guide" />
    </div>
    <div class="qr-icon-contain">
      <a href="https://goo.gl/8A1SJJ" class="qr-icon">
        <img :src="imagelist.android.url" alt="imagelist.android.alt" />
      </a>
      <a href="https://goo.gl/pG2PqW" class="qr-icon">
        <img :src="imagelist.ios.url" alt="imagelist.ios.alt" />
      </a>
    </div>
  </div>
</template>

<script>
import { type } from "os";

export default {
  name: "WeddingApp",
  props: {
    msg: String
  },
  data() {
    return {
      name: "",
      fullWidth: document.documentElement.clientWidth,
      fullHeight: document.documentElement.clientHeight,
      breakpoints: 3 /*xl:4 lg:3 md:2 sm:1 xs:0*/,
      lastScrollTop: 0,
      title_svg: require("@/assets/svg/title-05.svg"),
      imagelist: {
        android: {
          url: require("@/assets/appguide/app-QR-android.png"),
          alt: "android"
        },
        ios: {
          url: require("@/assets/appguide/app-QR-ios.png"),
          alt: "ios"
        }
      },
      photolist: [
        {
          url: require("@/assets/appguide/app_guide-01.jpg"),
          alt: "image"
        },
        {
          url: require("@/assets/appguide/app_guide-02.jpg"),
          alt: "image"
        },
        {
          url: require("@/assets/appguide/app_guide-03.jpg"),
          alt: "image"
        },
        {
          url: require("@/assets/appguide/app_guide-04.jpg"),
          alt: "image"
        }
      ]
    };
  },
  created() {
    let vm = this;
    window.addEventListener("resize", vm.handleResize);
    vm.checkBreakPoint();
  },
  mounted() {},
  computed: {
    userAgent() {
      let vm = this;
      return vm.$store.state.userAgent;
    }
  },
  methods: {
    handleResize(event) {
      let vm = this;
      vm.checkBreakPoint();
    },
    checkBreakPoint() {
      let vm = this;
      vm.fullWidth = document.documentElement.clientWidth;
      vm.fullHeight = document.documentElement.clientHeight;
      if (vm.fullWidth >= 1200) {
        vm.breakpoints = 4;
      } else if (vm.fullWidth >= 992) {
        vm.breakpoints = 3;
      } else if (vm.fullWidth >= 768) {
        vm.breakpoints = 2;
      } else if (vm.fullWidt >= 576) {
        vm.breakpoints = 1;
      } else {
        vm.breakpoints = 0;
      }
    }
  }
};
</script>

